.footer {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;

    &__text,
    &__mail {
        font-size: 1.4rem;
        color: white;
        margin-bottom: .4rem;
        text-decoration: none;

        @media (max-width: 768px) {
            font-size: .9rem;
            margin-bottom: .2rem;
            text-align: center;
        }
    }





}